<template>
  <div class="z-date-picker z-input" :style="{ width: breadth }">
    <v-menu
      ref="menu"
      v-model="menu"
      :close-on-content-click="false"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="auto"
      :z-index="zIndex"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
          v-model="value"
          :append-icon="appendIcon"
          :append-outer-icon="appendOuterIcon"
          :autofocus="autofocus"
          :clear-icon="clearIcon"
          :clearable="clearable"
			    :color="color"
          :counter="counter"
          :dense="dense"
          :disabled="disabled"
          :error="incorrect"
          :error-messages="errorMessage"
          :filled="filled"
          :hide-details="hideDetails"
          :hint="hint"
          :label="label"
          :outlined="outlined"
          :placeholder="placeholder"
          :prepend-inner-icon="prependIcon"
          :prepend-icon="prependOuterIcon"
          :readonly="readonly"
          :reverse="reverse"
          :solo="solo"
          :suffix="suffix"
          :success="success"
          v-bind="attrs"
          v-on="on"
          @click:clear="onClickClear"
          @input="onInput"
        >
          <!--   BEGIN -->
          <template v-slot:append>
            <slot name="append"></slot>
          </template>
          <!--   END -->

          <!--   BEGIN -->
          <template v-slot:append-outer>
            <slot name="append-outer"></slot>
          </template>
          <!--   END -->
          
          <!--   BEGIN -->
          <template v-slot:prepend-inner>
            <slot name="prepend"></slot>
          </template>
          <!--   END -->

          <!--   BEGIN -->
          <template v-slot:prepend>
            <slot name="prepend-outer"></slot>
          </template>
          <!--   END -->
        </v-text-field>
      </template>
      
      <!--  BEGIN -->
      <template v-if="mode === 'date'">
        <v-date-picker
          v-model="date"
          :first-day-of-week="firstDayOfWeek"
          :locale="locale"
          :no-title="noTitle"
          :range="range"
          :readonly="pickerReadonly"
          :type="pickerType"
          scrollable
          @change="onChangeDate"
        >
          <template v-if="!immediate">
            <v-spacer></v-spacer>
            <v-btn
              text
              color="primary"
              @click="menu = false"
            >
              
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="onConfirm"
            >
              
            </v-btn>
          </template>
        </v-date-picker>
      </template>
      <!--  END -->
      
      <!--  BEGIN -->
      <template v-else>
        <v-time-picker
          v-model="date"
          :first-day-of-week="firstDayOfWeek"
          :format="timeFormat"
          :locale="locale"
          :no-title="noTitle"
          :range="range"
          :readonly="pickerReadonly"
          scrollable
          :type="pickerType"
          :use-seconds="timeUseSeconds"
          @change="onChangeDate"
        >
          <template v-if="!immediate">
            <v-spacer></v-spacer>
            <v-btn
              text
              color="primary"
              @click="menu = false"
            >
              
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="onConfirm"
            >
              
            </v-btn>
          </template>
        </v-time-picker>
      </template>
      <!--  END -->
    </v-menu>
  </div>
</template>

<script>
  import FormMixins from '../mixins/FormMixins'
  import FormValidationMixins from '../mixins/FormValidationMixins'

  export default {
    name: 'ZDatePicker',
    mixins: [FormMixins, FormValidationMixins],

    props: {
      clearable: {
        type: Boolean,
        default: true
      },

      firstDayOfWeek: {
        type: [Number, String],
        default: 0
      },

      immediate: {
        type: Boolean,
        default: true
      },

      locale: {
        type: String,
        default: 'zh-cn'
      },

      mode: {
        validator(value) {
          return ~['date', 'month', 'time'].indexOf(value)
        },
        default: 'date'
      },

      noTitle: {
        type: Boolean,
        default: false
      },

      pickerReadonly: {
        type: Boolean,
        default: false
      },

      pickerType: {
        validator(value) {
          return ~['date', 'month'].indexOf(value)
        },
        default: 'date'
      },

      range: {
        type: Boolean,
        default: false
      },

      readonly: {
        type: Boolean,
        default: true
      },

      timeFormat: {
        validator(value) {
          return ~['ampm', '24hr'].indexOf(value)
        },
        default: 'ampm'
      },

      timeUseSeconds: {
        type: Boolean,
        default: false
      },

      zIndex: {
        type: [Number, String],
        required: false
      }
    },

    data() {
      return {
        date: undefined,
        menu: false
      }
    },

    methods: {
      onChangeDate() {
        if(this.immediate) {
          this.value = this.date
          this.menu = false
        }
      },

      onConfirm() {
        this.value = this.date
        this.menu = false
      },

      onClickClear(event) {
        this.date = ''
        this.value = this.date
        event.customValue = this.value
        this.verifyField()
        this.$emit('click:clear', event)
      },

      onInput() {
        console.log(this.value)
        this.verifyField()
      }
    },

    watch: {
      defaultValue: {
        handler(value) {
          this.date = value
        },
        immediate: true
      },

      date: {
        handler() {
          this.verifyField()
        }
      }
    }
  }
</script>