<template>
  <div class="z-autocomplete z-input" :style="{ width: breadth }">
    <v-autocomplete
      v-model="value"
      :allow-overflow="allowOverflow"
      :append-icon="appendIcon"
      :append-outer-icon="appendOuterIcon"
      :autofocus="autofocus"
      :clear-icon="clearIcon"
      :clearable="clearable"
			:color="color"
      :counter="counter"
      :dense="dense"
      :disabled="disabled"
      :error="incorrect"
      :error-messages="errorMessage"
      :filled="filled"
      :hide-details="hideDetails"
      :hint="hint"
      :items="items"
      :label="label"
      :loading="loading"
      :multiple="multiple"
      :noFilter="noFilter"
      :outlined="outlined"
      :persistent-hint="persistentHint"
      :persistent-placeholder="persistentPlaceholder"
      :placeholder="placeholder"
      :prepend-inner-icon="prependIcon"
      :prepend-icon="prependOuterIcon"
      :readonly="readonly"
      :reverse="reverse"
      :solo="solo"
      :suffix="suffix"
      :success="success"
      @blur="onBlur"
      @change="onChange"
      @click="onClick"
      @focus="onFocus"
      @input="onInput"
    >
      <!--   BEGIN -->
      <template v-slot:append>
        <slot name="append"></slot>
      </template>
      <!--   END -->

      <!--   BEGIN -->
      <template v-slot:append-outer>
        <slot name="append-outer"></slot>
      </template>
      <!--   END -->
      
      <!--   BEGIN -->
      <template v-slot:prepend-inner>
        <slot name="prepend"></slot>
      </template>
      <!--   END -->

      <!--   BEGIN -->
      <template v-slot:prepend>
        <slot name="prepend-outer"></slot>
      </template>
      <!--   END -->
    </v-autocomplete>
  </div>
</template>

<script>
  import FormMixins from '../mixins/FormMixins'
  import FormValidationMixins from '../mixins/FormValidationMixins'
  import { isArray, isYummy } from '../scripts/tools'

  export default {
    name: 'ZAutocomplete',
    mixins: [FormMixins, FormValidationMixins],
    props: {
      allowOverflow: {
        type: Boolean,
        default: true
      },

      dense: {
        type: Boolean,
        default: false
      },

      loading: {
        type: Boolean,
        default: false
      },

      multiple: {
        type: Boolean,
        default: false
      },

      noFilter: {
        type: Boolean,
        default: false
      },

      options: {
        type: Array,
        required: false
      }
    },

    data() {
      return {
        items: []
      }
    },

    methods: {
      onBlur(event) {
        this.$emit('blur', event, this.value)
      },

      onChange(value) {
        this.$emit('change', value)
      },

      onClick(event) {
        this.$emit('click', event, this.value)
      },

      onFocus(event) {
        this.$emit('focus', event, this.value)
      },

      onInput() {
        this.verifyField()
      },

      setOptions() {
        this.items = []

        if(isArray(this.options) && isYummy(this.options)) {
          for(let item of this.options) {
            this.items.push({
              text: item.label,
              value: item.value
            })
          }
        }
      }
    },

    watch: {
      options: {
        handler() {
          this.setOptions()
        },
        immediate: true
      }
    }
  }
</script>